<template>
  <div class="container mx-auto p-4">
    <!-- 使用全局注册的 BackHome 组件 -->
    <BackHome /> 

    <!-- 编辑文章表单 -->
    <div class="card bg-base-100 shadow-xl p-6">
      <div class="form-control mb-4">
        <label class="label">Title</label>
        <input
          v-model="article.title"
          type="text"
          placeholder="Enter the article title"
          class="input input-bordered"
        />
      </div>
      <div class="form-control mb-4">
        <label class="label">Content</label>
        <textarea
          v-model="article.content"
          placeholder="Enter the article content"
          class="textarea textarea-bordered"
        ></textarea>
      </div>
      <button @click="addart" class="btn btn-primary mt-4">Add Article</button>
    </div>

    <!-- 成功提示 -->
    <div role="alert" class="alert alert-info" v-if="isAlertVisible">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="h-6 w-6 shrink-0 stroke-current"
        fill="none"
        viewBox="0 0 24 24"
      >
        <path
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z"
        />
      </svg>
      <span>发布成功</span>
    </div>
  </div>
</template>

<script> 
import { DateTime } from "@/archive/dates.js";
import { adArtAPI,selArtAPI } from "@/archive/user.js";
export default {
  name: "AddArticle",
  data() {
    return {
      article: {
        title: "",
        content: "",
      },
      isAlertVisible: false, // 控制提示框是否显示
    };
  },
  methods: {
    // 发布文章
    async addart(articleId) {
      // 参数：title, content, DateTime,articleId
        const date = await adArtAPI(
          this.article.title,
          this.article.content,
          DateTime,
          this.$route.params.articleId
        );  
        
        if (date == 'ok') {
          // 显示发布成功提示
          const date = await selArtAPI(this.$route.params.articleId)
          this.$store.commit('setPosts', date);
          this.isAlertVisible = true;
          this.article.title = ''
          this.article.content = ''
          setTimeout(() => {
            this.isAlertVisible = false;
          }, 1000);
        } 
 
    },
  },
  created() {
    console.log(this.$route.params.articleId, "fabu");
  },
};
</script>

<style scoped>
.my-8{
    margin-top: 0rem;
    margin-bottom: 0rem;
 }

</style>
